#maindiv {
  width: 100%;
  margin-top: unset;
}
#editTeamNav{
  padding: 0% 3%;
  background: rgb(0, 174, 255);
  display: flex;
  justify-content: space-between;
  align-self: center;
  box-shadow: 0 0 5px black;
}
#desdiv {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  width: 20%;
}
#desdiv > i {
  color: maroon;
  font-size: 3rem;
}
#teamImg > img {
  width: 100%;
  height: 50%;
}
#firstName {
  margin-right: 5%;
}
#contentArea {
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-direction: column;
}
#TeamHolder {
  padding-top: 3%;
  display: flex;
  justify-content: space-evenly;
  align-items: center;
  flex-wrap: wrap;
  width: 80%;
  margin-top: 0 auto;
}

#TeamDetail {
  background: rgb(130, 196, 226);
  padding: 5px;
  border: 4px double grey;
  box-shadow: 0px 0px 5px black;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: flex-start;
  margin: 3% auto;
  border-radius: 20px;
  overflow: hidden;
}
#memdetail {
  padding: 0 3%;
}
#TeamDetail:hover {
  background-color: rgb(54, 125, 184);
  border: 4px solid rgb(16, 87, 116);
}

.buttonClassaz {
  font-size: 15px;
  font-family: Arial;
  width: 140px;
  height: 50px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  margin-top: 5%;
}

.buttonClassaz:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
.buttonClass2{
  font-size: 15px;
  font-family: Arial;
  width: 100px;
  height: 40px;
  border-width: 1px;
  color: #3a8a9e;
  border-color: #d6bcd6;
  font-weight: bold;
  border-top-left-radius: 15px;
  border-top-right-radius: 15px;
  border-bottom-left-radius: 15px;
  border-bottom-right-radius: 15px;
  box-shadow: 3px 4px 0px 0px #899599;
  text-shadow: 0px 1px 0px #e1e2ed;
  background: linear-gradient(#ededed, #bab1ba);
  margin-top: 5%;
}

.buttonClass2:hover {
  background: linear-gradient(#bab1ba, #ededed);
}
#addMember {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  height: 40vh;
  padding: 0 3%;
  box-shadow: 0px 0px 5px grey;
  background-color: navajowhite;
  z-index: 5;
  position: absolute;
  left: 65%;
  top: 40%;
  display: none;
}
.addform {
  border: none;
  outline: none;
  border-bottom: 4px double black;
  background-color: transparent;
}
